// src/pages/notice/noticeDetail.js  =>    
import React, { useState, useEffect, useContext } from 'react';
import { useParams, useNavigate } from 'react-router-dom';  //     
import apiClient from '../../utils/axios';
import styles from './FaqDetail.module.css';
import { AuthContext } from '../../AuthProvider';

const FaqDetail = () => {
    const { no } = useParams();  // URL  no  ()
    // const history = useHistory();  //     (navigate  )
    // react 6.0  deprecated 
    const navigate = useNavigate();  //useNavigate  
    const [faqQ, setFaqQ] = useState(null);  //      
    const [faqA, setFaqA] = useState(null); 
    const [error, setError] = useState(null);  //      

    const { isLoggedIn, role, accessToken } = useContext(AuthContext);  // AuthProvider  

    useEffect(() => {
        
        //     ajax      
        const fetchFaqDetail = async () => {
            console.log('no : ' + no);
            try {
                // url path  ${}     (``)   (  : )
                const response = await apiClient.get(`/faq/${no}`);
                setFaqQ(response.data.Q);  //    
                setFaqA(response.data.A || null);
            } catch (error) {
                setError('   !');
                console.error(error);
            }
        };

        //  
        fetchFaqDetail();
    }, [no]);

    const handleDelete = async () => {
        if (window.confirm(' ?')) {
            try {
                await apiClient.delete(`/faq/${no}`, {
                    headers: {
                        Authorization: `Bearer ${accessToken}`, // accessToken 
                    },
                });
                alert(' .');
                //  ,     <=   
                //history.push('/faq');
                navigate('/faq');  //   
            } catch (error) {
                console.error('Delete error : ', error);
                alert(' !');
            }
        }
    };

    if (faqQ == null) {
        return <div className={styles.loading}> ...</div>; //  
    }
    
    if (error) {
        return <div className={styles.error}>{error}</div>; //   
    }

    return (
        <div>
            <h2> { no }  </h2>
            <table border="1">
                <tbody>
                    <tr>
                        <th></th>
                        <td>{faqQ.faqTitle}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{faqQ.category}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{faqQ.createdAt}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{faqQ.faqContent}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{faqQ.viewCount}</td>
                    </tr>
                </tbody>
            </table>
            {faqA && (
            <>
            <h2> { no }  </h2>
            <table border="1">
                <tbody>
                    <tr>
                        <th></th>
                        <td>{faqA.faqTitle}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{faqA.category}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{faqA.createdAt}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{faqA.faqContent}</td>
                    </tr>
                </tbody>
            </table>
            </>
            )}
            {/* ADMIN       */}
            {isLoggedIn && role === 'ADMIN' ? (
                <div>
                    <button onClick={handleDelete}></button>
                    <button onClick={() => navigate(`/faq`)}></button>
                </div>
            ) : (
                <div>
                    <button onClick={() => navigate(`/faq`)}></button>
                </div>
            )}
                
        </div>
    );

};  // const FaqDetail

export default FaqDetail;