// src/pages/notice/NoticeWrite.js
import React, { useEffect, useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import apiClient from '../../utils/axios';
import styles from './FaqWrite.module.css';
import { AuthContext } from '../../AuthProvider';

function FaqWrite() {
    const { userid, accessToken } = useContext(AuthContext); // AuthProvider  

    const [answerContent, setAnswerContent] = useState("");

    //  
    const [formData, setFormData] = useState({
        faqTitle: '',
        faqWriter: '', //  
        category: '',     
        faqContent: '',
        answerContent: '',
    });

    //   'FaqList'    
    const navigate = useNavigate();

    //   (window.onload or jquery.document.ready  )
    useEffect(() => {   
        setFormData((prevFormData) => ({
            ...prevFormData,
            faqWriter: userid, // AuthProvider   userid
        }));
        
    }, [userid]);

    const handleChange = (e) => {
        const { name, value } = e.target;
        if(name === 'answerContent'){
            setAnswerContent(value);
        }
        setFormData((prevFormData) => ({
            ...prevFormData,
            [name]: value,

        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault(); //     (submit  )

        const data = new FormData();
        data.append('faqTitle', formData.faqTitle);
        data.append('faqWriter', formData.faqWriter);
        data.append('category', formData.category);
        data.append('faqContent', formData.faqContent);

        try {
            await apiClient.post('/faq', data, {
                params: {
                    'answerContent': answerContent
                },
                headers: {
                    'Content-Type': 'multipart/form-data',
                    Authorization: `Bearer ${accessToken}`, // accessToken 
                }
            });

            alert('   ');
            //         

            navigate('/faq');
        } catch (error) {
            console.error('   ', error);
            alert('   ');
        }
    };

    return (
        <div className={styles.container}>
        <h1 className={styles.header}>  </h1>
        <form 
            onSubmit={handleSubmit}
            enctype="multipart/form-data"
            className={styles.form}>
            <table id="outer" align="center" width="700" cellspacing="5" cellpadding="5">   
                <tbody>
                <tr><th width="120"> </th>
                    <td>
                        <input 
                            type="text" 
                            name="faqTitle" 
                            size="50"
                            value={formData.faqTitle}
                            onChange={handleChange}
                            placeholder="   ."
                            required />         
                    </td></tr>
                <tr><th width="120"></th>
                    <td>
                        <input 
                            type="text" 
                            name="faqWriter" 
                            value={formData.faqWriter}
                            readonly />         
                    </td></tr>
                <tr><th width="120"></th>
                    <td>
                        <select
                            name="category" 
                            value={formData.category}
                            onChange={handleChange}
                            required
                        >
                            <option value=" TOP"> TOP</option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>       
                    </td></tr>
                <tr><th> </th>
                    <td><textarea 
                            rows="5" 
                            cols="50" 
                            name="faqContent"
                            value={formData.faqContent}
                            onChange={handleChange}
                            placeholder="  ."
                            required
                            ></textarea>
                    </td></tr>
                <tr><th> </th>
                <td><textarea 
                        rows="5" 
                        cols="50" 
                        name="answerContent"
                        value={answerContent}
                        onChange={handleChange}
                        placeholder="  ."
                        required
                        ></textarea>
                </td></tr>
                </tbody>
            </table>
        </form>
        <div className={styles.button}>
                <div className="button-group">
                    <input type="submit" value="" 
                    onClick={handleSubmit}/> &nbsp;
                    <input type="reset" value=" " 
                        onClick={() => {setFormData({ ...formData, faqTitle: '',
                            faqWriter: formData.faqWriter, //   
                            category: '',
                            faqContent: '',
                        });
                        setAnswerContent('');
                    }}
                         />{' '} &nbsp;
                    <input 
                        type="button" 
                        value="" 
                        onClick={() => {
                            navigate(-1);   //  history.go(-1);  
                        }} />
                </div>
        </div>
    </div>
    );
}

export default FaqWrite;
