import React, { useState, useEffect, useContext } from "react";
import { useParams, useNavigate } from "react-router-dom"; //     
import apiClient from "../../utils/axios";
import styles from "./NoticeDetail.module.css";
import { AuthContext } from "../../AuthProvider";

const NoticeDetail = () => {
  const { id } = useParams(); // URL  no  ()
  const navigate = useNavigate(); //useNavigate  
  const [notice, setNotice] = useState(null); //      
  const [error, setError] = useState(null); //      
  const [loading, setLoading] = useState(true); //  

  const { isLoggedIn, role, accessToken } = useContext(AuthContext);

  useEffect(() => {
    //     ajax      
    const fetchNoticeDetail = async () => {
      try {
        setLoading(true);
        const response = await apiClient.get(`/notice/${id}`);
        setNotice(response.data);
      } catch (error) {
        console.error("Error fetching notice details:", error);
        setError("   !");
      } finally {
        setLoading(false);
      }
    };

    //  
    fetchNoticeDetail();
  }, [id]);

  const handleFileDownload = async (ofileName, rfileName) => {
    try {
      const response = await apiClient.get("/notice/nfdown", {
        params: {
          ofile: ofileName,
          rfile: rfileName,
        },
        responseType: "blob", //   
      });

      //  
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", ofileName);
      document.body.appendChild(link);
      link.click();
      link.remove();
    } catch (error) {
      console.error("File download error : ", error);
      alert("  .");
    }
  };

  const handleMoveEdit = () => {
    navigate(`/notice/edit/${id}`);
  };

  const handleDelete = async (rfile) => {
    if (window.confirm(" ?")) {
      try {
        await apiClient.delete(`/notice/${id}`, {
          params: { rfile: rfile },
          headers: {
            Authorization: `Bearer ${accessToken}`, // accessToken 
          },
        });
        alert(" .");
        //  ,     <=   
        //history.push('/notice');
        navigate("/notice"); //   
      } catch (error) {
        console.error("Delete error : ", error);
        alert(" !");
      }
    }
  };

  if (!notice) {
    return <div className={styles.loading}> ...</div>; //  
  }

  if (error) {
    return <div className={styles.error}>{error}</div>; //   
  }

  return (
    <div className={styles.detailcontainer}>
      <h2 className={styles.detailtitle}> {id}  </h2>
      <table border="2">
        <tbody>
          <tr>
            <th></th>
            <td>{notice.ntitle}</td>
          </tr>
          <tr>
            <th></th>
            <td>{notice.nnickname}</td>
          </tr>
          <tr>
            <th></th>
            <td>
              {notice.ofileName ? (
                <button
                  onClick={() =>
                    handleFileDownload(notice.ofileName, notice.rfileName)
                  }
                >
                  {notice.ofileName}
                </button>
              ) : (
                " "
              )}
            </td>
          </tr>
          <tr>
            <th></th>
            <td>{notice.ncreatedAt}</td>
          </tr>
          <tr>
            <th></th>
            <td>{notice.ncontent}</td>
          </tr>
          <tr>
            <th></th>
            <td>{notice.ncount}</td>
          </tr>
        </tbody>
      </table>
      {/* ADMIN       */}
      <div className={styles.buttongroup}>
        {isLoggedIn && role === "ADMIN" && (
          <>
            <button onClick={handleMoveEdit} className={styles.editbutton}>
                
            </button>
            <button
              onClick={() => handleDelete(notice.rfileName)}
              className={styles.deletebutton}
            >
              
            </button>
          </>
        )}
        <button
          onClick={() => navigate("/notice")}
          className={styles.backbutton}
        >
          
        </button>
      </div>
    </div>
  );
}; // const NoticeDetail

export default NoticeDetail;
