// src/pages/qna/QnaDetail.js
import React, { useState, useEffect, useContext } from 'react';
import { useParams, useNavigate } from 'react-router-dom';  //     
import apiClient from '../../utils/axios';
import styles from './QuestionDetail.module.css';
import { AuthContext } from '../../AuthProvider';
import Modal from '../../components/common/Modal';
import AnswerWrite from './AnswerWrite';

const QuestionDetail = () => {
  const { qno } = useParams();  // URL  no  ()
  // const history = useHistory();  //     (navigate  )
  // react 6.0  deprecated 
  const navigate = useNavigate();  //useNavigate  
  const [question, setQuestion] = useState(null);  //      
  const [answers, setAnswers] = useState([]);  //   
  const [error, setError] = useState(null);  //      

  //  
  const [showModal, setShowModal] = useState(false);
  //  |    
  const [answerTarget, setAnswerTarget] = useState(null);

  //  |    
  const [editingAnswer, setEditingAnswer] = useState(null);  //  (ID) 
  const [editingTitle, setEditingTitle] = useState('');  //    
  const [editingContent, setEditingContent] = useState('');  //    

  const { isLoggedIn, userid, role, accessToken } = useContext(AuthContext); //AuthProvider  

  useEffect(() => {
      //     
      //     ajax      
      const fetchQnaDetail = async () => {
          console.log('no : ' + qno);
          try {
              // url path  ${}     (``)   (  : )
              const response = await apiClient.get(`/question/detail/${qno}`);
              setQuestion(response.data.question);  //    
              setAnswers(response.data.answerList);  //   
              console.log(response.data.question);
              console.log(response.data.answerList);
          } catch (error) {
              setError('   !');
              console.error(error);
          }
      };

      //  
      fetchQnaDetail();
  }, [qno]);

  //   
  const openModal = ({ qno, ano = null }) => {
      setAnswerTarget({ qno, ano });
      setShowModal(true);
  };

  //   
  const closeModal = () => {
      setShowModal(false);
      setAnswerTarget(null);
      window.location.reload();  //   
  };


  const handleMoveEdit = () => {
      navigate(`/question/update/${qno}`);
  };

  const handleDelete = async () => {
      if (window.confirm(' ?')) {
          try {
              await apiClient.delete(`/question/${qno}`, {
                  headers: {
                      Authorization: `Bearer ${accessToken}`, // accessToken 
                  },
              });
              alert(' .');
              //  ,     <=   
              //history.push('/notice');
              navigate('/qna');  //   
          } catch (error) {
              console.error('Delete error : ', error);
              alert(' !');
          }
      }
  };

  const handleAnswerDelete = async (ano) => {
      if(window.confirm(' ?')){
          try{
              await apiClient.delete(`/answer/${ano}`, {
                  headers: {
                      Authorization: `Bearer ${accessToken}`, // accessToken 
                  },
              });
              setAnswers((prevAnswers) => prevAnswers.filter((answer) => answer.ano !== ano));
              alert(' .');

              window.location.reload();
          }catch(error){
              alert('  .');
          }
      }
  };

  //  |    ,   input    
  const handleAnswerEdit = (ano, atitle, acontent) => {
      setEditingAnswer(ano);
      setEditingTitle(atitle);
      setEditingContent(acontent);
  };

  //  |       
  const handleSaveAnswerEdit = async (ano) => {
      try {
          await apiClient.put(`/answer/${ano}`, {
              ano: ano,
              atitle: editingTitle,
              acontent: editingContent
          }, {
              headers: { Authorization: `Bearer ${accessToken}`}
          });

          setAnswers((prevAnswers) => prevAnswers.map((answer) => answer.ano === ano 
                                          ? { ...answer, atitle: editingTitle, acontent: editingContent} 
                                          : answer));
          setEditingAnswer(null);
          alert(' .');
      } catch (error) {
          alert('  .');
      }
  };

  

  if (!question) {
      return <div className={styles.loading}> ...</div>; //  
  }
  
  if (error) {
      return <div className={styles.error}>{error}</div>; //   
  }

  return (
      <div>
          <h2>{question.qtitle}</h2>
          <table border="1">
              <tbody>
                  <tr>
                      <th></th>
                      <td>{question.qwriter}</td>
                  </tr>
                  <tr>
                      <th></th>
                      <td>{question.qdate}</td>
                  </tr>
                  <tr>
                      <th></th>
                      <td>{question.qcontent}</td>
                  </tr>
              </tbody>
          </table>
          {/*     */}
          <div className={styles.buttonGroup}>
              <button className={styles.actionButton} onClick={() => navigate(-1)}></button>
          
          {/*      ,      */}
          {isLoggedIn && userid === question.qwriter && question.answerYn === 'N' ? (
              <>
                  <button onClick={handleMoveEdit}></button>
                  <button onClick={() => handleDelete()}></button>
              </> ) : (isLoggedIn && role === 'ADMIN' && question.answerYn === 'N' && (                   
                      <button onClick={() => openModal({ qno: question.qno })}></button>                    
              ))
          }
          </div>
          <h3></h3>
          <table className={styles.answerTable}>
              <thead>
                  <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th>|</th>
                  </tr>
              </thead>
              <tbody>
                  {answers.map((answer) => (
                      <tr key={answer.ano} 
                          className={answer.replyLev === 2 ? styles.replyIndented : styles.replyItem}>
                          <td>{answer.awriter}</td>
                          <td>
                              {editingAnswer === answer.ano ? (
                                  <input type="text"
                                      value={editingTitle}
                                      onChange={(e) => setEditingTitle(e.target.value)} />
                              ) : (
                                  answer.atitle
                              )}
                          </td>
                          <td>
                              {editingAnswer === answer.ano ? (
                                  <input type="text"
                                      value={editingContent}
                                      onChange={(e) => setEditingContent(e.target.value)} />
                              ) : (
                                  answer.acontent
                              )}                               
                          </td>
                          <td>{answer.aDate}</td>
                          <td>
                            {isLoggedIn && userid === answer.awriter && (
                              editingAnswer === answer.ano ? (
                                <button onClick={() => handleSaveAnswerEdit(answer.ano)}></button>
                            ) : (
                            <>
                                <button onClick={() => handleAnswerEdit(answer.ano, answer.atitle, answer.acontent)}></button>
                                <button onClick={() => handleAnswerDelete(answer.ano)}></button>
                            </>
                            ))}
          
                          </td>
                      </tr>
                  ))}
              </tbody>
          </table>

          {/*    */}
          {showModal && (
              <Modal onClose={closeModal}>
                  <AnswerWrite qno={answerTarget.qno}
                      ano={answerTarget.ano}
                      onAnswerAdded={closeModal} />
              </Modal>
          )}

      </div>
  );

};  // const BoardDetail

export default QuestionDetail;