import React, { useState } from "react";
import axios from "../../../utils/axios";
import Modal from "../../../components/common/Modal"; // Modal  
import Login from "../../member/login/Login"; // Login  
import styles from "./FindId.module.css";

function FindId() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [foundId, setFoundId] = useState(null);
  const [error, setError] = useState("");
  const [showLoginModal, setShowLoginModal] = useState(false); //   

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setFoundId(null);
    setError("");

    try {
      const response = await axios.get("/member/findId", {
        params: { name, email },
      });
      setFoundId(response.data.id);
    } catch (error) {
      console.error(error.response?.data || error.message);
      setError(
        error.response?.data?.message ||
          "   .   ."
      );
    } finally {
      setIsLoading(false);
    }
  };

  const handleOpenLoginModal = () => {
    setShowLoginModal(true); //   
  };

  const handleCloseLoginModal = () => {
    setShowLoginModal(false); //   
  };

  return (
    <div className={styles.container}>
      <h2> </h2>
      <form onSubmit={handleSubmit} className={styles.form}>
        <div className={styles.inputGroup}>
          <label htmlFor="name"></label>
          <input
            type="text"
            id="name"
            placeholder=" "
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className={styles.inputGroup}>
          <label htmlFor="email"></label>
          <input
            type="email"
            id="email"
            placeholder=" "
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <button type="submit" disabled={isLoading} className={styles.button}>
          {isLoading ? " ..." : " "}
        </button>
      </form>

      {foundId && (
        <div className={styles.successSection}>
          <p className={styles.successMessage}>
              <strong>{foundId}</strong> .
          </p>
          <div className={styles.buttonGroup}>
            <button
              className={`${styles.button} ${styles.secondaryButton}`}
              onClick={() => (window.location.href = "/findPassword")}
            >
               
            </button>
            <button
              className={`${styles.button} ${styles.primaryButton}`}
              onClick={handleOpenLoginModal} //   
            >
              
            </button>
          </div>
        </div>
      )}

      {error && <p className={styles.errorMessage}>{error}</p>}

      {/*   */}
      {showLoginModal && (
        <Modal onClose={handleCloseLoginModal}>
          <Login onLoginSuccess={handleCloseLoginModal} />
        </Modal>
      )}
    </div>
  );
}

export default FindId;
