// src/pages/qna/QnaDetail.js
import React, { useState, useEffect, useContext } from "react";
import { useParams, useNavigate } from "react-router-dom"; //     
import apiClient from "../../utils/axios";
import styles from "./QuestionUpdate.module.css";
import { AuthContext } from "../../AuthProvider";

const QuestionUpdate = () => {
  const { qno } = useParams(); // URL  no  ()
  const navigate = useNavigate(); //  

  const { accessToken } = useContext(AuthContext);

  //     
  const [formData, setFormData] = useState({
    qno: "",
    qtitle: "",
    qwriter: "",
    qcontent: "",
    qdate: "",
  });

  const [error, setError] = useState(null); //      

  //   
  useEffect(() => {
    //     ajax      
    const fetchQuestionUpdate = async () => {
      try {
        // url path  ${}     (``)   (  : )
        const response = await apiClient.get(`/question/detail/${qno}`);
        console.log(response.data);

        //form   
        setFormData({
          qno: response.data.question.qno,
          qtitle: response.data.question.qtitle,
          qwriter: response.data.question.qwriter,
          qcontent: response.data.question.qcontent,
          qdate: response.data.question.qdate,
        }); //    
        console.log(formData);
      } catch (error) {
        setError("   !");
        console.error(error);
      }
    };

    //  
    fetchQuestionUpdate();
  }, [qno]);

  //input    
  const handleChange = (e) => {
    const { name, value } = e.target;

    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault(); //     (submit  )

    const data = new FormData();
    Object.entries(formData).forEach(([key, value]) => data.append(key, value));

    try {
      //post  put     url   
      //  : put  url  id(pk)      
      await apiClient.put(`/question/${qno}`, data, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${accessToken}`, // accessToken 
        },
      });

      alert("  ");
      //       
      // navigate(`/question/detail/${qno}`);
      navigate("/qna");
    } catch (error) {
      console.error("  ", error);
      alert("  ");
    }
  };

  if (error) {
    return <div className={styles.error}>{error}</div>; //   
  }

  return (
    <div className={styles.container}>
      <h2> {qno}  </h2>
      <form onSubmit={handleSubmit} className={styles.form}>
        <table
          id="outer"
          align="center"
          width="700"
          cellspacing="5"
          cellpadding="5"
        >
          <tbody>
            <tr>
              <th width="120"> </th>
              <td>
                <input
                  type="text"
                  id="qno"
                  name="qno"
                  size="50"
                  value={formData.qno}
                  readOnly
                />
              </td>
            </tr>
            <tr>
              <th width="120"> </th>
              <td>
                <input
                  type="text"
                  id="qtitle"
                  name="qTitle"
                  size="50"
                  value={formData.qtitle}
                  onChange={handleChange}
                  required
                />
              </td>
            </tr>
            <tr>
              <th width="120"></th>
              <td>
                <input
                  type="text"
                  id="qwriter"
                  name="qWriter"
                  value={formData.qwriter}
                  readonly
                />
              </td>
            </tr>
            <tr>
              <th> </th>
              <td>
                <textarea
                  rows="5"
                  cols="50"
                  name="qcontent"
                  value={formData.qcontent}
                  onChange={handleChange}
                  required
                ></textarea>
              </td>
            </tr>

            <tr>
              <th colspan="2">
                <input type="submit" value="" /> &nbsp;
                <input
                  type="reset"
                  value=""
                  onClick={() => navigate(`/board/detail/${qno}`)}
                />
              </th>
            </tr>
          </tbody>
        </table>
      </form>
    </div>
  );
};

export default QuestionUpdate;
