import React, { useRef, useEffect, forwardRef, useImperativeHandle, useState } from "react";
import styles from "./FaceIDLogin.module.css";

const FaceIDLogin = forwardRef((props, ref) => {
  const videoRef = useRef(null); //   
  const canvasRef = useRef(null); //   
  const [isWebcamActive, setIsWebcamActive] = useState(false); //   

  //   
  const startWebcam = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ video: true }); //   
      if (videoRef.current) {
        videoRef.current.srcObject = stream; //   
        videoRef.current.play(); //  
        setIsWebcamActive(true); //  
      }
    } catch (error) {
      console.error("  :", error);
      alert("   .   .");
    }
  };

  //   
  const stopWebcam = () => {
    if (videoRef.current) {
      const stream = videoRef.current.srcObject;
      if (stream) {
        stream.getTracks().forEach((track) => track.stop()); //    
      }
      videoRef.current.srcObject = null; //  
      setIsWebcamActive(false); //  
    }
  };

  //     
  useEffect(() => {
    return () => stopWebcam();
  }, []);

  //    
  const captureImage = () => {
    if (!isWebcamActive) {
      alert("  .   .");
      return null;
    }

    const video = videoRef.current;
    const canvas = canvasRef.current;
    const context = canvas.getContext("2d");

    //     
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;

    //    
    context.drawImage(video, 0, 0, canvas.width, canvas.height);

    //      
    return canvas.toDataURL("image/jpeg");
  };

  //    
  useImperativeHandle(ref, () => ({
    captureImage, //   
    startWebcam, //   
    stopWebcam, //   
  }));

  return (
    <div className={styles.container}>
      <h3 className={styles.header}> </h3>

      {/*     */}
      {!isWebcamActive && (
        <div className={styles.videoPlaceholder}>  </div>
      )}

      {/*   */}
      <video
        ref={videoRef}
        autoPlay
        playsInline
        className={`${styles.video} ${!isWebcamActive ? styles.hidden : ""}`}
      ></video>

      {/*  /  */}
      <button onClick={isWebcamActive ? stopWebcam : startWebcam} className={styles.button}>
        {isWebcamActive ? " " : " "}
      </button>

      <canvas ref={canvasRef} className={styles.hidden}></canvas>
    </div>
  );
});

export default FaceIDLogin;
