// src/pages/member/Signup.js
import React, { useEffect, useState } from "react";
import apiClient from "../../../utils/axios";
import style from "./Signup.module.css";

function Signup({ onSignupSuccess }) {
  const [formData, setFormData] = useState({
    memberId: "",
    userNickName: "",
    userPwd: "",
    confirmPwd: "",
    name: "",
    gender: "",
    phone: "",
  });

  // input      formData  property    
  //   input     
  //   input    handleChange  useState    ( )
  const handleChange = (e) => {
    const { name, value } = e.target;

    setFormData({ ...formData, [name]: value });
  };

  const [isIdAvailable, setIsIdAvailable] = useState(null);

  //       
  const handleIdCheck = async () => {
    if (!formData.memberId) {
      alert(" .");
      return;
    }

    try {
      const response = await apiClient.post("/member/memberidchk", null, {
        params: { memberId: formData.memberId },
      });

      if (response.data === "ok") {
        setIsIdAvailable(true);
        alert("  .");
      } else {
        setIsIdAvailable(false);
        alert("  .    .");
      }
    } catch (error) {
      console.error("   : ", error);
      alert("   .  .");
    }
  };

  //  input    
  const validate = () => {
    //    
    if (formData.pw !== formData.confirmPwd) {
      alert("   .  .");
      return false;
    }

    //   
    return true;
  };

  //  input  (focus)     
  const handleConfirmPwd = () => {
    if (formData.confirmPwd) {
      validate();
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault(); // submit   (axios    )

    if (isIdAvailable === false) {
      alert("  .");
      return;
    }

    if (isIdAvailable === null) {
      alert("  .");
      return;
    }

    //     
    if (!validate()) {
      return;
    }

    // FormData  
    const combinedFormData = new FormData();
    //input    append 
    Object.keys(formData).forEach((key) => {
      combinedFormData.append(key, formData[key]);
    });

    try {
      //   
      const response = await apiClient.post("/member", combinedFormData, {
        headers: { "Content-Type": "multipart/form-data" },
      });

      if (response.status === 200) {
        alert(" .");
        onSignupSuccess(); // Header.js  onSignupSuccess={handleSignupSuccess}  
      }
    } catch (error) {
      console.error("  : ", error);
      alert("  .   ");
    }
  };

  return (
    <div className={style.container}>
      <h1> </h1>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="memberId">: </label>
          <input
            type="text"
            id="memberId"
            name="memberId"
            value={formData.memberId}
            onChange={handleChange}
            required
          />
          <button type="button" onClick={handleIdCheck}>
            
          </button>
        </div>
        <div>
          <label htmlFor="userPwd">: </label>
          <input
            type="password"
            id="userPwd"
            name="pw"
            value={formData.pw}
            onChange={handleChange}
            required
          />
        </div>
        <div>
          <label htmlFor="confirmPwd"> : </label>
          <input
            type="password"
            id="confirmPwd"
            name="confirmPwd"
            value={formData.confirmPwd}
            onChange={handleChange}
            onBlur={handleConfirmPwd}
            required
          />
        </div>
        <div>
          <label htmlFor="userName">: </label>
          <input
            type="text"
            id="userName"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
          />
        </div>
        <div>
          <label htmlFor="userName">: </label>
          <input
            type="text"
            id="nickName"
            name="nickname"
            value={formData.nickname}
            onChange={handleChange}
            required
          />
        </div>
        <div>
          <label htmlFor="gender">: </label>
          <select
            id="gender"
            name="gender"
            value={formData.gender}
            onChange={handleChange}
            required
          >
            <option value=""></option>
            <option value="M"></option>
            <option value="F"></option>
          </select>
        </div>
        <div>
          <label htmlFor="phone">: </label>
          <input
            type="tel"
            id="phone"
            name="phoneNumber"
            value={formData.phoneNumber}
            onChange={handleChange}
            required
          />
        </div>
        <button type="submit"></button>
      </form>
    </div>
  );
}

export default Signup;
