// src/components/common/PagingView.js
import React from "react";
import styles from "./PagingView.module.css";

const PagingView = ({
  currentPage,
  maxPage,
  startPage,
  endPage,
  onPageChange,
}) => {
  const groupSize = 10; //   (    10)
  const currentGroup = Math.ceil(currentPage / groupSize); //   
  //,      (,   )  
  //const startPage = (currentGroup - 1) * groupSize + 1; //   
  //const endPage = Math.min(currentGroup * groupSize, maxPage);  //   

  //      
  const pages = Array.from(
    { length: endPage - startPage + 1 },
    (_, i) => startPage + i
  );

  return (
    <div className={styles.pagingContainer}>
      {/* 1   */}
      <button disabled={currentPage === 1} onClick={() => onPageChange(1)}>
        1
      </button>
      {/*      */}
      <button
        disabled={startPage === 1}
        onClick={() => onPageChange(startPage - 1)}
      >
        
      </button>
      {/*      */}
      {pages.map((page) => (
        <button
          key={page}
          className={currentPage === page ? styles.activePage : ""}
          onClick={() => onPageChange(page)}
        >
          {page}
        </button>
      ))}
      {/*      */}
      <button
        disabled={endPage === maxPage}
        onClick={() => onPageChange(endPage + 1)}
      >
        
      </button>
      {/*    */}
      <button
        disabled={currentPage === maxPage}
        onClick={() => onPageChange(maxPage)}
      >
        {maxPage}
      </button>
    </div>
  );
};

export default PagingView;
