import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import apiClient from "../../utils/axios";
import styles from "./MyPageUpdate.css";
import { AuthContext } from "../../AuthProvider";

function MyPageUpdate() {
  const { uuid, userid, accessToken } = useContext(AuthContext);
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    name: "",
    nickname: "",
    memberid: "",
    phoneNumber: "",
    gender: "",
    google: "",
    naver: "",
    kakao: "",
  });

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        setLoading(true);
        const response = await apiClient.get(`/member/mypage/${userid}`, {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        });
        setFormData({
          memberid: response.data.memberid,
          name: response.data.name,
          nickname: response.data.nickname,
          phoneNumber: response.data.phoneNumber,
          gender: response.data.gender,
          google: response.data.google,
          naver: response.data.naver,
          kakao: response.data.kakao,
        });
      } catch (err) {
        console.error("Error fetching user data:", err);
        setError("   .");
      } finally {
        setLoading(false);
      }
    };

    fetchUserData();
  }, [userid, accessToken]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSave = async () => {
    try {
      const response = await apiClient.put(`/member/mypage/${uuid}`, formData, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      });
      alert("   .");
      navigate("/mypage"); //     
    } catch (err) {
      console.error("Error updating user data:", err);
      alert("   .");
    }
  };

  if (loading) {
    return <div className={styles.loading}> ...</div>;
  }

  if (error) {
    return <div className={styles.error}>{error}</div>;
  }

  return (
    <div className="mypage-edit-container">
      <h1 className="mypage-edit-title"> </h1>
      <form className="edit-form">
        <label>
          *:
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleInputChange}
            required
          />
        </label>
        <label>
          :
          <input
            type="text"
            name="nickname"
            value={formData.nickname}
            onChange={handleInputChange}
          />
        </label>
        <label>
          :
          <input
            type="text"
            name="phoneNumber"
            value={formData.phoneNumber}
            onChange={handleInputChange}
          />
        </label>

        <p></p>
        <label> </label>
        <label>
          :
          <input
            type="text"
            name="google"
            value={formData.google}
            onChange={handleInputChange}
          />
        </label>
        <label>
          :
          <input
            type="text"
            name="naver"
            value={formData.naver}
            onChange={handleInputChange}
          />
        </label>
        <label>
          :
          <input
            type="text"
            name="kakao"
            value={formData.kakao}
            onChange={handleInputChange}
          />
        </label>

        <button type="button" onClick={handleSave} className="save-btn">
          
        </button>
      </form>
    </div>
  );
}

export default MyPageUpdate;
