// src/pages/notice/NoticeUpdate.js
import React, { useState, useEffect, useContext } from "react";
import { useParams, useNavigate } from "react-router-dom"; //     
import apiClient from "../../utils/axios";
import styles from "./PostsEdit.module.css";
import { AuthContext } from "../../AuthProvider";

const PostsEdit = () => {
  const { accessToken } = useContext(AuthContext);
  const { id } = useParams(); // URL  no  ()
  const navigate = useNavigate(); //  

  //     
  const [formData, setFormData] = useState({
    postId: "",
    title: "",
    writer: "",
    nickname: "",
    content: "",
    fileName: "",
    renameFile: "",
  });
  //  formData   
  const [file, setFile] = useState(null); // 

  const [error, setError] = useState(null); //      

  //   
  useEffect(() => {
    //     ajax      
    const fetchPostsDetail = async () => {
      try {
        // url path  ${}     (``)   (  : )
        const response = await apiClient.get(`/posts/${id}`);
        console.log(response.data);

        //form   
        setFormData({
          postId: response.data.postId,
          title: response.data.title,
          writer: response.data.writer,
          nickname: response.data.nickname,
          content: response.data.content,
          fileName: response.data.fileName,
          renameFile: response.data.renameFile,
        }); //    
        console.log(formData);
      } catch (error) {
        setError("   !");
        console.error(error);
      }
    };

    //  
    fetchPostsDetail();
  }, [id]);

  //input    
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;

    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: type === "checkbox" ? (checked ? "Y" : "N") : value,
    }));
  };

  //  
  const handleFileChange = (e) => {
    setFile(e.target.files[0]); // input    file  
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault(); //     (submit  )

    const data = new FormData();
    data.append("postId", formData.postId);
    data.append("title", formData.title);
    data.append("writer", formData.writer);
    data.append("nickname", formData.nickname);
    data.append("content", formData.content);
    data.append("fileName", formData.fileName);
    data.append("renameFile", formData.renameFile);
    if (file) {
      data.append("ofile", file); //  
    }

    try {
      //post  put     url   
      //  : put  url  id(pk)      
      await apiClient.put(`/posts/${id}`, data, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${accessToken}`, // accessToken 
        },
      });

      alert("  ");
      navigate(`/posts/${id}`);
    } catch (error) {
      console.error("  ", error);
      alert("  ");
    }
  };

  if (error) {
    return <div className={styles.error}>{error}</div>; //   
  }

  return (
    <div>
      <h2> {id}  </h2>
      <form
        onSubmit={handleSubmit}
        enctype="multipart/form-data"
        className={styles.form}
      >
        <table
          id="outer"
          align="center"
          width="700"
          cellspacing="5"
          cellpadding="5"
        >
          <tbody>
            <tr>
              <th width="120"> </th>
              <td>
                <input
                  type="text"
                  id="postId"
                  name="postId"
                  size="50"
                  value={formData.postId}
                  readOnly
                />
              </td>
            </tr>
            <tr>
              <th width="120"> </th>
              <td>
                <input
                  type="text"
                  id="title"
                  name="title"
                  size="50"
                  value={formData.title}
                  onChange={handleChange}
                  required
                />
              </td>
            </tr>
            <tr>
              <th width="120"></th>
              <td>
                <input
                  type="text"
                  id="nickname"
                  name="nickname"
                  value={formData.nickname}
                  readonly
                />
              </td>
            </tr>
            <tr>
              <th></th>
              <td>
                <input type="file" name="file" onChange={handleFileChange} />
                {formData.fileName && (
                  <div>
                    <span>  : {formData.fileName}</span>
                  </div>
                )}
              </td>
            </tr>
            <tr>
              <th> </th>
              <td>
                <textarea
                  rows="5"
                  cols="50"
                  name="content"
                  value={formData.content}
                  onChange={handleChange}
                  required
                ></textarea>
              </td>
            </tr>

            <tr>
              <th colspan="2">
                <input type="submit" value="" /> &nbsp;
                <input
                  type="reset"
                  value=""
                  onClick={() => navigate(`/posts/${id}`)}
                />
              </th>
            </tr>
          </tbody>
        </table>
      </form>
    </div>
  );
};

export default PostsEdit;
