import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import apiClient from "../../utils/axios";
import styles from "./MyPageMain.css";
import { AuthContext } from "../../AuthProvider";

function MyPage() {
  const { userid, accessToken } = useContext(AuthContext);
  const navigate = useNavigate();

  const [userData, setUserData] = useState({
    name: "",
    nickname: "",
    memberId: "",
    phoneNumber: "",
    gender: "",
    enrollDate: "",
    lastModified: "",
    google: "",
    naver: "",
    kakao: "",
  });

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchMyPage = async () => {
      try {
        setLoading(true);
        const response = await apiClient.get(`/member/mypage/${userid}`, {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        });
        setUserData(response.data);
      } catch (err) {
        console.error("Error fetching user data:", err);
        setError("   .");
      } finally {
        setLoading(false); //  
      }
    };

    fetchMyPage();
  }, [userid, accessToken]);

  const handleEditClick = () => {
    navigate("/mypage/mypageUpdate"); //    
  };

  if (loading) {
    return <div className={styles.loading}> ...</div>;
  }

  if (error) {
    return <div className={styles.error}>{error}</div>;
  }

  return (
    <div className="mypage-container">
      <h1 className="mypage-title"></h1>
      <div className="user-info">
        <p>
          <strong>:</strong> {userData.name || " "}
        </p>
        <p>
          <strong>:</strong> {userData.nickname || " "}
        </p>
        <p>
          <strong>:</strong> {userData.memberId || " "}
        </p>
        <p>
          <strong>:</strong> {userData.phoneNumber || " "}
        </p>
        <p>
          <strong>:</strong>
          {userData.gender === "F"
            ? "Female"
            : userData.gender === "M"
              ? "Male"
              : " "}
        </p>
        <p>
          <strong> :</strong> {userData.enrollDate || " "}
        </p>
        <p>
          <strong> :</strong>
          {userData.google || ""}
        </p>
        <p>
          <strong> :</strong>
          {userData.naver || ""}
        </p>
        <p>
          <strong> :</strong>
          {userData.kakao || ""}
        </p>
      </div>
      <button className="update-btn" onClick={handleEditClick}>
         
      </button>
    </div>
  );
}

export default MyPage;
