import React, { useEffect, useState, useContext } from "react";
import apiClient from "../../utils/axios";
import styles from "./MypageAdmin.css";
import PagingView from "../../components/common/PagingView"; // PagingView  import
import { AuthContext } from "../../AuthProvider";

function MypageAdmin() {
  const { accessToken } = useContext(AuthContext);
  const [members, setMembers] = useState([]);
  const [pagingInfo, setPagingInfo] = useState({
    currentPage: 1,
    maxPage: 1,
    startPage: 1,
    endPage: 1,
  }); //  
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  //    
  const fetchMembers = async (page) => {
    try {
      setLoading(true);

      // API 
      const response = await apiClient.get(`/member/admin/members`, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        params: {
          page: page,
        },
      });

      setMembers(response.data.list); //   
      setPagingInfo(response.data.paging); //   
    } catch (err) {
      console.error("Error fetching members:", err);
      setError("   .");
    } finally {
      setLoading(false);
    }
  };

  //    
  const handleLoginOkChange = async (uuid, loginOk) => {
    try {
      const response = await apiClient.put(
        `member/loginok/${uuid}/${loginOk}`,
        {}, // Request body  
        {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        }
      );

      if (response.status === 200) {
        alert("   .");
        setMembers((prev) =>
          prev.map((member) =>
            member.uuid === uuid ? { ...member, loginOk } : member
          )
        );
      }
    } catch (err) {
      console.error("Error updating loginOk:", err);
      alert("    .");
    }
  };

  //       
  useEffect(() => {
    fetchMembers(1);
  }, []);

  //   
  const handlePageChange = (page) => {
    fetchMembers(page);
  };

  if (loading) return <div className={styles.loading}> ...</div>;
  if (error) return <div className={styles.error}>{error}</div>;

  return (
    <div className="admin-page-container">
      <h1 className="admin-page-title"> </h1>
      <table className="members-table">
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th> </th>
            <th> </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {members.map((member) => (
            <tr key={member.memberId}>
              <td>{member.memberId}</td>
              <td>{member.name}</td>
              <td>{member.google}</td>
              <td>{member.adminYn}</td>
              <td>
                <select
                  value={member.loginOk}
                  onChange={(e) =>
                    handleLoginOkChange(member.uuid, e.target.value)
                  }
                >
                  <option value="Y"></option>
                  <option value="N"></option>
                </select>
              </td>
              <td>{member.enrollDate}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <PagingView
        currentPage={pagingInfo.currentPage || 1}
        maxPage={pagingInfo.maxPage || 1}
        startPage={pagingInfo.startPage || 1}
        endPage={pagingInfo.endPage || 1}
        onPageChange={(page) => handlePageChange(page)}
      />
    </div>
  );
}

export default MypageAdmin;
