import React, { createContext, useState, useEffect } from "react";
import axios from "axios";

// Context 
export const AuthContext = createContext();

// Access Token  
const parseAccessToken = (token) => {
  if (!token) return null;
  try {
    const base64Url = token.split(".")[1];
    const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
    const jsonPayload = decodeURIComponent(
      atob(base64)
        .split("")
        .map((c) => `%${("00" + c.charCodeAt(0).toString(16)).slice(-2)}`)
        .join("")
    );
    return JSON.parse(jsonPayload);
  } catch (error) {
    console.error("Access Token  :", error);
    return null;
  }
};

// Context Provider 
export const AuthProvider = ({ children }) => {
  const [authInfo, setAuthInfo] = useState({
    isLoggedIn: false,
    accessToken: "",
    refreshToken: "",
    role: "",
    username: "",
    userid: "",
    uuid: "",
  });

  //  
  const logout = async () => {
    try {
      const refreshToken = localStorage.getItem("refreshToken");
      if (refreshToken) {
        console.log("   .");
        await axios.post(
          "http://localhost:8888/logout",
          {},
          {
            headers: {
              Authorization: `Bearer ${refreshToken}`,
            },
          }
        );
        console.log("   .");
      }
    } catch (error) {
      console.error("   :", error);
    } finally {
      localStorage.removeItem("accessToken");
      localStorage.removeItem("refreshToken");
      setAuthInfo({
        isLoggedIn: false,
        accessToken: "",
        refreshToken: "",
        role: "",
        username: "",
        userid: "",
        uuid: "",
      });
      console.log(" : authInfo .");
    }
  };

  // Access Token  
  const refreshAccessToken = async () => {
    try {
      const refreshToken = localStorage.getItem("refreshToken");
      if (!refreshToken) throw new Error("Refresh Token .");

      console.log("Access Token   .");
      const response = await axios.post(
        "http://localhost:8888/reissue",
        {},
        {
          headers: {
            Authorization: `Bearer ${refreshToken}`,
          },
        }
      );

      const newAccessToken = response.data.accessToken;
      if (!newAccessToken)
        throw new Error(" Access Token   .");

      console.log("Access Token  :  Access Token .");
      localStorage.setItem("accessToken", newAccessToken);
      const parsedToken = parseAccessToken(newAccessToken);

      setAuthInfo((prev) => ({
        ...prev,
        isLoggedIn: true,
        accessToken: newAccessToken,
        role: parsedToken?.role || prev.role,
        username: parsedToken?.name || prev.username,
        userid: parsedToken?.sub || prev.userid,
        uuid: parsedToken?.uuid || prev.uuid,
      }));
    } catch (error) {
      if (error.response && error.response.status === 401) {
        console.error("Refresh Token . .");
      } else {
        console.error("Access Token    :", error);
      }
      logout();
    }
  };

  //  
  const login = ({ accessToken, refreshToken }) => {
    if (!accessToken || !refreshToken) {
      console.error(
        "  Access Token  Refresh Token ."
      );
      return;
    }

    localStorage.setItem("accessToken", accessToken);
    localStorage.setItem("refreshToken", refreshToken);

    const parsedToken = parseAccessToken(accessToken);
    if (!parsedToken) {
      console.error("  Access Token  .");
      logout();
      return;
    }

    setAuthInfo({
      isLoggedIn: true,
      accessToken,
      refreshToken,
      role: parsedToken?.role || "USER",
      username: parsedToken?.name || "  ",
      userid: parsedToken?.sub || "  ",
      uuid: parsedToken?.uuid || "  ",
    });

    console.log(" : authInfo .");
  };

  //     
  useEffect(() => {
    const initializeAuthState = async () => {
      try {
        const storedAccessToken = localStorage.getItem("accessToken");
        const storedRefreshToken = localStorage.getItem("refreshToken");

        if (!storedRefreshToken) {
          console.log(
            " Refresh Token .   ."
          );
          logout();
          return;
        }

        const isRefreshTokenExpired =
          parseAccessToken(storedRefreshToken)?.exp * 1000 < Date.now();
        if (isRefreshTokenExpired) {
          console.warn("Refresh Token . .");
          logout();
          return;
        }

        if (storedAccessToken) {
          const parsedToken = parseAccessToken(storedAccessToken);
          if (parsedToken && parsedToken.exp * 1000 > Date.now()) {
            console.log(
              " Access Token .   ."
            );
            setAuthInfo({
              isLoggedIn: true,
              accessToken: storedAccessToken,
              refreshToken: storedRefreshToken || "",
              role: parsedToken.role || "user",
              username: parsedToken.name || "  ",
              userid: parsedToken.sub || "  ",
              uuid: parsedToken.uuid || "  ",
            });
          } else {
            console.warn(
              "Access Token .    ."
            );
            await refreshAccessToken();
          }
        } else {
          console.log(
            " Access Token .    ."
          );
          await refreshAccessToken();
        }
      } catch (error) {
        console.error("   , :", error);
        logout();
      }
    };

    initializeAuthState();
  }, []);

  // Access Token  
  useEffect(() => {
    const checkAndRefreshToken = async () => {
      try {
        const accessToken = localStorage.getItem("accessToken");
        if (!accessToken) return;

        const parsedToken = parseAccessToken(accessToken);
        if (!parsedToken) {
          console.error(" Access Token .");
          logout();
          return;
        }

        const expirationTime = parsedToken.exp * 1000 - Date.now();
        if (expirationTime < 60000) {
          console.log("Access Token  .  .");
          await refreshAccessToken();
        } else {
          console.log(
            `Access Token   :\n >>${Math.floor(expirationTime / 1000 / 60)} ${Math.floor((expirationTime / 1000) % 60)}`
          );
        }
      } catch (error) {
        console.error("Access Token     :", error);
      }
    };

    const intervalId = setInterval(checkAndRefreshToken, 50000);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <AuthContext.Provider
      value={{ ...authInfo, login, logout, refreshAccessToken }}
    >
      {children}
    </AuthContext.Provider>
  );
};
