// src/pages/notice/NoticeUpdate.js
import React, { useState, useEffect, useContext } from "react";
import { useParams, useNavigate } from "react-router-dom"; //     
import apiClient from "../../utils/axios";
import styles from "./NoticeEdit.module.css";
import { AuthContext } from "../../AuthProvider";

const NoticeEdit = () => {
  const { accessToken } = useContext(AuthContext);
  const { id } = useParams(); // URL  no  ()
  const navigate = useNavigate(); //  

  //     
  const [formData, setFormData] = useState({
    noticeNo: "",
    nTitle: "",
    nWriter: "",
    nNickname: "",
    nContent: "",
    ofileName: "",
    rfileName: "",
  });
  //  formData   
  const [file, setFile] = useState(null); // 

  const [error, setError] = useState(null); //      

  //   
  useEffect(() => {
    //     ajax      
    const fetchNoticeDetail = async () => {
      try {
        // url path  ${}     (``)   (  : )
        const response = await apiClient.get(`/notice/${id}`);
        console.log(response.data);

        //form   
        setFormData({
          noticeNo: response.data.noticeNo,
          nTitle: response.data.ntitle,
          nWriter: response.data.nwriter,
          nNickname: response.data.nnickname,
          nContent: response.data.ncontent,
          ofileName: response.data.ofileName,
          rfileName: response.data.rfileName,
        }); //    
        console.log(formData);
      } catch (error) {
        setError("   !");
        console.error(error);
      }
    };

    //  
    fetchNoticeDetail();
  }, [id]);

  //input    
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;

    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: type === "checkbox" ? (checked ? "Y" : "N") : value,
    }));
  };

  //  
  const handleFileChange = (e) => {
    setFile(e.target.files[0]); // input    file  
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault(); //     (submit  )

    const data = new FormData();
    data.append("noticeNo", formData.noticeNo);
    data.append("nTitle", formData.nTitle);
    data.append("nWriter", formData.nWriter);
    data.append("nNickname", formData.nNickname);
    data.append("nContent", formData.nContent);
    data.append("rfileName", formData.rfileName);
    data.append("ofileName", formData.ofileName);
    if (file) {
      data.append("ofile", file); //  
    }

    try {
      //post  put     url   
      //  : put  url  id(pk)      
      await apiClient.put(`/notice/${id}`, data, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${accessToken}`, // accessToken 
        },
      });

      alert("   ");
      //       
      navigate(`/notice/${id}`);
    } catch (error) {
      console.error("  ", error);
      alert("   ");
    }
  };

  if (error) {
    return <div className={styles.error}>{error}</div>; //   
  }

  return (
    <div className={styles.container}>
      <h2 className={styles.header}> {id}  </h2>
      <form
        onSubmit={handleSubmit}
        enctype="multipart/form-data"
        className={styles.form}
      >
        <table
          id="outer"
          align="center"
          width="700"
          cellspacing="5"
          cellpadding="5"
        >
          <tbody>
            <tr>
              <th width="120"> </th>
              <td>
                <input
                  type="text"
                  id="noticeNo"
                  name="noticeNo"
                  size="50"
                  value={formData.noticeNo}
                  readOnly
                />
              </td>
            </tr>
            <tr>
              <th width="120"> </th>
              <td>
                <input
                  type="text"
                  id="nTitle"
                  name="nTitle"
                  size="50"
                  value={formData.nTitle}
                  onChange={handleChange}
                  required
                />
              </td>
            </tr>
            <tr>
              <th width="120"></th>
              <td>
                <input
                  type="text"
                  id="nNickname"
                  name="nNickname"
                  value={formData.nNickname}
                  readonly
                />
              </td>
            </tr>
            <tr>
              <th></th>
              <td>
                <input type="file" name="file" onChange={handleFileChange} />
                {formData.ofileName && (
                  <div>
                    <span>  : {formData.ofileName}</span>
                  </div>
                )}
              </td>
            </tr>
            <tr>
              <th> </th>
              <td>
                <textarea
                  rows="5"
                  cols="50"
                  name="nContent"
                  value={formData.nContent}
                  onChange={handleChange}
                  required
                ></textarea>
              </td>
            </tr>

            <tr>
              <th colspan="2">
                <input type="submit" value="" /> &nbsp;
                <input
                  type="reset"
                  value=""
                  onClick={() => navigate(`/notice/${id}`)}
                />
              </th>
            </tr>
          </tbody>
        </table>
      </form>
    </div>
  );
};

export default NoticeEdit;
