/* 
[face-api.js   => .js ]
 -  : ssdMobilenetv1, faceLandmark68Net, faceRecognitionNet 
 -   cdn 

:
await faceapi.nets.ssdMobilenetv1.loadFromUri("path/to/weights/");
await faceapi.nets.faceLandmark68Net.loadFromUri("path/to/weights/");
await faceapi.nets.faceRecognitionNet.loadFromUri("path/to/weights/");


   :
const detections = await faceapi
    .detectAllFaces(video)
    .withFaceLandmarks()
    .withFaceDescriptors();
*/

import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import Webcam from "react-webcam";
import * as faceapi from "face-api.js";
import styles from "./FaceIdRegister.module.css"; // CSS  

function FaceIdRegister() {
    const [statusMessage, setStatusMessage] = useState("  ...");
    const [userUuid, setUserUuid] = useState("");
    const [modelsLoaded, setModelsLoaded] = useState(false); //   
    const [faceDetected, setFaceDetected] = useState(false);
    const [captureTimer, setCaptureTimer] = useState(null); // 3  
    const [imageCaptured, setImageCaptured] = useState(false); //   
    const webcamRef = useRef(null);
    const detectionIntervalRef = useRef(null); //    

    // JWT  UUID 
    const getPayloadFromToken = (token) => {
        if (!token) return null;
        try {
            const base64Url = token.split(".")[1];
            const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
            const decodedPayload = atob(base64);
            return JSON.parse(decodedPayload);
        } catch (e) {
            console.error("JWT  :", e);
            return null;
        }
    };

    useEffect(() => {
        const token = localStorage.getItem("accessToken");
        const payload = getPayloadFromToken(token);

        if (payload && payload.uuid) {
            setUserUuid(payload.uuid);
            console.log("Extracted UUID:", payload.uuid);
        } else {
            setStatusMessage("   .");
            console.log("No UUID found in token.");
        }

        // Load face-api.js models from CDN
        const loadModels = async () => {
            try {
                setStatusMessage("  ...");
                console.log("  ...");
                await faceapi.nets.ssdMobilenetv1.loadFromUri(
                    "https://cdn.jsdelivr.net/gh/justadudewhohacks/face-api.js/weights/"
                );
                await faceapi.nets.faceLandmark68Net.loadFromUri(
                    "https://cdn.jsdelivr.net/gh/justadudewhohacks/face-api.js/weights/"
                );
                await faceapi.nets.faceRecognitionNet.loadFromUri(
                    "https://cdn.jsdelivr.net/gh/justadudewhohacks/face-api.js/weights/"
                );

                setModelsLoaded(true); //    
                setStatusMessage("  !  .");
                console.log("  ");
            } catch (error) {
                console.error("  :", error);
                setStatusMessage("  !");
            }
        };

        loadModels();
    }, []);

    useEffect(() => {
        if (modelsLoaded) {
            //       
            console.log("      ");
            startDetectingFaces();
        }

        return () => {
            //     
            stopDetectingFaces();
        };
    }, [modelsLoaded]);

    const startDetectingFaces = () => {
        detectionIntervalRef.current = setInterval(async () => {
            console.log("detectFace  ");

            if (webcamRef.current && webcamRef.current.video.readyState === 4) {
                const video = webcamRef.current.video;
                const detections = await faceapi
                    .detectAllFaces(video)
                    .withFaceLandmarks()
                    .withFaceDescriptors();

                if (detections.length > 0) {
                    setFaceDetected(true);
                    setStatusMessage("  ! 3 .");

                    //       
                    if (!imageCaptured && !captureTimer) {
                        const timer = setTimeout(() => {
                            sendImageToServer();
                            stopDetectingFaces(); //   
                        }, 3000);

                        setCaptureTimer(timer);
                    }
                } else {
                    setFaceDetected(false);
                    setStatusMessage(" .");
                }
            }
        }, 1000); // 1  
    };

    const stopDetectingFaces = () => {
        if (detectionIntervalRef.current) {
            clearInterval(detectionIntervalRef.current);
            detectionIntervalRef.current = null;
        }
    };

    const sendImageToServer = async () => {
        if (!userUuid || imageCaptured) {
            return; // UUID      
        }

        try {
            const imageSrc = webcamRef.current.getScreenshot();
            if (!imageSrc) {
                setStatusMessage("  !");
                return;
            }

            const response = await axios.post("http://localhost:5001/register-faceid", {
                image: imageSrc,
                uuid: userUuid,
            });

            if (response.status === 200) {
                setStatusMessage(" !  .");
                setImageCaptured(true); //   
                console.log("Face ID  :", response.data);
            } else {
                setStatusMessage("Face ID  !");
            }
        } catch (error) {
            console.error("Face ID  :", error);
            setStatusMessage("Face ID  !");
        } finally {
            if (captureTimer) clearTimeout(captureTimer);
            setCaptureTimer(null);
        }
    };

    const retryCapture = async () => {
        if (!imageCaptured) {
            setStatusMessage("   .");
            return;
        }

        try {
            //    
            const response = await axios.post("http://localhost:5001/delete-faceid", {
                uuid: userUuid,
            });

            if (response.status === 200) {
                setStatusMessage("  .   .");
                setImageCaptured(false); //      
                setFaceDetected(false); //    
                startDetectingFaces(); //   
            } else {
                setStatusMessage("   !");
            }
        } catch (error) {
            console.error("   :", error);
            setStatusMessage("   !");
        }
    };

    const goBack = () => {
        window.history.back(); //   
    };

    return (
        <div className={styles.faceIdContainer}>
            <h1 className={styles.title}>Face ID </h1>
            <div className={styles.webcamWrapper}>
                <Webcam
                    audio={false}
                    ref={webcamRef}
                    screenshotFormat="image/jpeg"
                    width="100%"
                    height="100%"
                />
            </div>
            <p>{statusMessage}</p>

            {/*   */}
            <button onClick={goBack}></button>

            {/*    (   ) */}
            {imageCaptured && <button onClick={retryCapture}> </button>}
        </div>
    );
}

export default FaceIdRegister;
